@import url(https://fonts.googleapis.com/css?family=Overlock:400,400i,700|Oleo+Script);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,100,300,500,700,900);
html{
    /*Hsl Values*/
    --black: #3c3c3c;
    --dark-green: #164a41;
    --medium-green: #4d774e;
    --light-green: #9dc88d;
    --yellow: #f1b24a;
    --light: #f1f1f1;

    /*Font Size*/
    --fs-xl: 2.5rem;
    --fs-lg: 2.0rem;
    --fs-md: 1.5rem;
    --fs-sm: 1rem;
    
    /*Font Weight*/
    --text-normal: 500;
    --text-bold: 900;
}
@media (min-width: 1000px){
    html{
        --fs-xl: 3.5rem;
        --fs-lg: 3.0rem;
        --fs-md: 1.5rem;
        --fs-sm: 1.125rem;
    }
}

/*Global Styling*/
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behaviour: smooth;
}
img{
    width: 100%;
    display: block;
}
body{
    background: #164a41;
    background: var(--dark-green);
    color: #f1f1f1;
    color: var(--light);
    font-family: 'Roboto', sans-serif;
}
h1, h2,h3,h4,h5{
    font-family: 'Overlock', Arial, Helvetica, sans-serif;
}
a{
    text-decoration: none;
}
ul{
    list-style: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media (max-width: 900px){
    header{
        position: fixed;
        display: -webkit-flex;
        display: flex;
        width: 100%;
        height: 3.2rem;
        background: var(--medium-green);
        z-index: 999;
    }
    .footer{
        width: 100%;
        margin-top: 1rem;
        background: var(--medium-green);
    }
    .nav{
        -webkit-flex-grow: 1;
                flex-grow: 1;
    }
    .nav__list{
        display: -webkit-flex;
        display: flex;
        width: 100%;
    }
    .nav__list li{
        margin-left: auto;
        margin-top: 10px;
    }
    .nav__list li:nth-last-of-type(1){
        margin-right: auto;
    }
    .nav__list li a{
        color: var(--light);
        font-size: 1.5rem;
    }
    .nav__list li a:hover,
    .nav__list li a:active{
        color: var(--yellow);
    }
    .footer .nav__list li{
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }
}
@media (min-width: 900px){
    header{
        display: grid;
        place-items: center;
        height: 95vh;
        position: fixed;
        left: 0;
        top: 0;
        bottom: 0;
    }
    .footer{
        display: grid;
        place-items: center;
        height: 95vh;
        position: fixed;
        right: 0;
        top: 0;
        bottom: 0;
    }
    .nav__list{
        height: 100%;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .nav__list li{
