  }
  .about-me p {
    color: var(--yellow);
    text-align: center;
  }
  .languages{
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-between;
              justify-content: space-between;
  }
  .icons{
      width: 50px;
  }
